
<template lang="en">
    <div  class="my-popups">
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">Report
                        </h5>                        
                        <button @click="$emit('closeAddMorepopup')" type="button" class="close"
                            data-original-title="Close"><em class="fa fa-times"></em></button>

                    </div>
                    <div class="border p-3">
                        <div class="col-lg-12 mb-3 px-0">
                            <div class="heading-border">
                                <h5 class="h5"><span>Select report(s) from below list to display on dashboard.</span></h5>
                            </div>
                        </div>
                       
                        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0 float-left">
                            <div class="table-responsive table-fix-header">
                                <table  class="table table-bordered mb-0" id="tblReportDashboard">
                                    <thead class="thead-bg">
                                        <tr>
                                            <th class="text-center">
                                                <input type="checkbox" v-model="selectAllPram"  @change="SelectAll"/>
                                            </th>
                                            <th>Report</th>
                                            <th>Description</th>
                                        </tr>
                                    </thead>
                                    <tbody>

                                        <tr v-for="(item,index) in ReportData" :key="index">  
                                            <td>
        
                                                <input type="checkbox" v-model="item.ISSELECTED"   @change="SingleSelect" />

                                            </td>                                          
                                            <td>
                                              <span> {{item.REPORT_HEADING}}</span>>
                                            </td>
                                            <td>
                                            <span>{{item.REPORT_DESCRIPTION}}</span>
                                            </td>
                                            
                                         </tr>
                                    </tbody>
                              
                                </table>
                            </div>
                        
                    </div>
                    <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left px-0">
                        <div class="row flex-row-reverse">
                            <div class="col-lg-4 text-right" >

                                <a href="javascript:;" v-on:click="SaveWidgets()"
                                    class="btn btn-success formbtn text-white mr-1" :title="$t('Save')"><i
                                        class="fa fa-save pr-2"></i>
                                    <span>{{ $t('Save') }}</span></a>
                                <a href="javascript:;" @click="ClosePopup()" :title="$t('Cancel')"
                                    class="btn btn-danger formbtn text-white"><i class="fa fa-close pr-2"></i><span>{{
                                        $t('Cancel') }}</span></a>
                            </div>

                        </div>
                    </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataService from '../../service/DataService';

export default {
    props: {
        ReportData: [],

    },
    data() {
        return {
            mulipleSelect:false,
            Sreportdata:[],
            selectAllPram:false,
            ReportIds:[]
        }

    },
    created() {
        var vm=this;
        //vm.SaveWidgets();
    },
    methods: {
        closeAddMorepopup() {
            var vm = this
            vm.AddAseet = false;
        },


        SelectAll(){
            debugger
      
           var vm=this;

           if(vm.selectAllPram){
            vm.ReportData.forEach(evt => {

                evt.ISSELECTED=1;                      

                })

           }
           else{
            vm.ReportData.forEach(evt => {

                evt.ISSELECTED=0;                      

                })

           }
          

        },

        SingleSelect(){
            debugger
            var vm=this;
        vm.selectAllPram=true;
        vm.ReportData.forEach(evt => {

                    if(evt.ISSELECTED!=1){

                        vm.selectAllPram=false;
                    }                     

                })
          
        },

      SaveWidgets(){
        debugger;
        debugger
        var vm=this
        var Reportid =""
        vm.ReportData.forEach(ele => {
            if(ele.ISSELECTED==true ){
                vm.ReportIds.push(ele.REPORT_ID);
            }
        })
         Reportid=vm.ReportIds.join(',');
        let JsonObj={
          ReportIds:Reportid,
            ModuleName:''
        }
        // var Jsondata=JSON.stringify(JsonObj);
        DataService.widgetSave(JsonObj).then(response=>{
            
           console.log('res',response) 
           vm.$parent.AssetDashBoard();
           vm.$parent.closeAddMorepopup(); 
        })
      }

    }

};


</script>