<template>
    <section>
        <div class="padding-t_10">
            <div class="row">
                <div class="col-md-12 mb-2">
                    <div class="theme-primary partition  p-2" style="overflow:visible">
                        <span class="p-name text-white p-0">{{ $t('Product Dashboard') }}</span>
                        <div class="float-right global" >
                            <DateRangespop :pgaction="global"></DateRangespop>
                        </div>
                    </div>
                </div>
            </div>
           
            <div id="tabs" class="">
               
                <div class="dashbord-graph-widget row ml-0 mr-0">
                    <div v-for="item in DashData" class="col-md-4">
                        <div :style="{ transform: `scale(${item.zoomLevel})` }">
                        <div data-id="@item.REPORT_ID"
                            class="graph-widget align-self-stretch col-sm-12 col-md-6 col-lg-6 col-xl-4 mb-3 ticketingDashboardHead"
                            daction="@list.ACTION" dctrl="@list.CONTROLLER">
                            <div class="graph-widget-box gwb_control">
                                <div class="graph-widget-heading"> {{ item.REPORT_HEADING }}
                                    <input type="hidden"
                                        value="@Resources.ResourceManager.GetString(@list.REPORT_HEADING)" />
                                    <span class="icon_close">

                                        <a class="hlkDeleteReport ancdashboard mobileleftalignfix text-secondary"
                                            @click="Removereport(item.REPORT_ID)"><i class="fa fa-times"></i></a>
                                    </span>
                                    <span class="icon_expand">
                                        <a class="zoomin ancdashboard mobileleftalignfix text-secondary"
                                            @click="zoomin(item)"><i class="fa fa-expand"></i></a>
                                    </span>

                                    <div class="float-right local">
                                        <DateRangespop :pgaction="item.ACTION"></DateRangespop>
                                    </div>

                                    <div class="float-right">
                                        <VueApexCharts width="600" type="pie" :options="item.pieChart.chartOptions"
                                            :series="item.pieChart.series"></VueApexCharts>
                                    </div>

                                    <div class="clearfix"></div>

                                </div>

                                <div class="clearfix"></div>
                                <div class="raph-widget-content reportBlock text-center" action="@Asset.ACTION"
                                    controller="@list.CONTROLLER">

                                    <!-- <div id="@list.REPORT_HEADING"></div> -->
                                </div>



                            </div>
                        </div>
                    </div>

                    </div>

                </div>


                <div class="clearfix"></div>
                <div class="text-right mt-3">
                    <a id="hlkAddMoreReports" class="btn btn-success formbtn" @click="openAddMorepopup()">
                        <i class="fa fa-plus pr-2"></i>
                        {{ $t('AddMore') }}
                    </a>
                </div>
            </div>
            <AddMorepop :ReportData="ReportData" v-if="AddAseet" @closeAddMorepopup="closeAddMorepopup"> </AddMorepop>
            <div>

            </div>
            
        </div>
    </section>
</template>

<script>
import AddMorepop from '../../components/Modal/AddMorepop';
import VueApexCharts from 'vue-apexcharts'
import DateRangespop from '../../components/Modal/DateRangespop.vue'
import DataService from '../../service/DataService';


export default {
    components: {
        DateRangespop,
        AddMorepop,
        VueApexCharts
    },
    props: {
    pgaction: {
      type: String,
    }
  },
    data() {
        
        return {

            PieWidgetData: [],
            DashData: [],
            AddAseet: false,
            ReportData: [],
            zoomLevel: 1.0,
            
            
        }

    },
    created() {

        var vm = this;
        vm.AssetDashBoard("");
        vm.GetReporttype();

    },

    methods: {
        

        GetReporttype: async function () {
            debugger;
            var vm = this;
            var url = `moduleName=Asset`
            await DataService.GetReports(url).then(response => {
                debugger
                vm.ReportData = response.data.Result;

            })
        },
        openAddMorepopup: function () {
            var vm = this
            vm.AddAseet = true;
        },

        closeAddMorepopup() {
            var vm = this
            vm.AddAseet = false;
        },
        AssetDashBoard: async function (actionType) {
            //debugger;
            var vueObj = this;
            var url=``;
            if(actionType==undefined || actionType==''){
                 url= `fromDate=${$('.global #txtFromDate_undefined').val()}&toDate=${$('.global #txtToDate_undefined').val()}`

                 await DataService.GetDashBoard(url).then(response => {
                if (response.data != null) {
                    vueObj.DashData = response.data.ReportList;
                    var listLables = [];
                    var seriesList = [];
                    var res = {};
                    vueObj.DashData.forEach(evt => {
                        debugger;

                        listLables = [];
                        seriesList = [];
                        evt.chartList.forEach(function (value, i) {
                            listLables.push(value.STATUS_NAME);
                            seriesList.push(value.TOTAL_COUNT);

                        });


                        res = {
                            series: seriesList,
                            chartOptions: {
                                chart: {
                                    width: 380,
                                    type: 'pie',
                                },
                                labels: listLables,
                                responsive: [{
                                    breakpoint: 480,
                                    options: {
                                        chart: {
                                            width: 200
                                        }
                                    }
                                }]
                            }


                        }
                        evt.pieChart = res;
                        evt.zoomLevel=.9

                    })
                }
            });

            }
            else{
                debugger
                var fromParam='#txtFromDate_'+actionType;
                var toParam='#txtToDate_'+actionType;
                var url= `fromDate=${$(fromParam).val()}&toDate=${$(toParam).val()}&reportType=${actionType}`
                await DataService.LocalAssetReportTypeParam(url).then(response => {
                if (response.data != null) {
                 //   vueObj.DashData = response.data.ReportList;                    
                  
                }
            });
            }
          
            // var url=`fromDate=${vueObj.fromDate}&toDate=${vueObj.toDate}`
          
        },

        zoomin(item) {
            debugger; 
      // Increase the zoom level
      item.zoomLevel += 1;
    },
        Removereport(value) {
            var vm = this;
            var url = `reportId=${value}`;
            DataService.ReportsRemove(url).then(response => {
                if (response.data) {
                    vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("REPORT_ID")), function () {

                    }
                    )
                }
                else {
                    vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Id')]), "failure", true, vm.$t('Alert'));
                }

            })

        },







    }


}
</script>

<style>
.unique_dropdown {
    background: #484848;
    line-height: 20px;
    height: 33px;
    color: #ffffff;
    margin-top: 3px;
    padding: 5px 3px 5px 0px;
    border: 1px solid #767676;
}

.table_header .date_range_bi {
    background: #484848 !important;
    border: 1px solid #767676 !important;
}

/*.ticketingDashboardHead:nth-child(3n+2) {
        margin-left: 0.5%;
    }*/
.ticketingDashboardHead:nth-child(3n+3) {
    float: right !important;
}

.contentcontainer {
    margin-top: 0px !important;
}

.DatePopUp {
    background-color: #FFFFFF;
    border: 1px solid #030303;
    box-shadow: 0 0 3px #D8D8D8;
    position: absolute;
    width: 244px;
    z-index: 999;
    right: 4px;
}

.ui-datepicker-trigger {
    display: none !important;
}

.table_header .date_range_bi {
    padding: 5px 0 5px 5px;
    margin-right: 6px;
}

.zoomin {
    background-position: -95px 1px;
    width: 25px;
}

.reporthead .icon_remove {
    width: 26px;
}

.table_header .date_range label {
    margin: 0 5px 0 0;
}</style>